<script>

import {store} from "@/store/store";

export default class PayplugOauthLogin {
  title = null;
  name = null;
  email_label = null;
  email_placeholder = null;
  password_label = null;
  password_placeholder = null;
  connexion = null;
  create_account = null;
  create_account_url = null;
  forgot_password = null;
  forgot_password_url = null;
  error = null;
  sso_title = null;
  sso_description = null;
  sso_information = null;
  sso_button = null;
  sso_button_url = null;

  constructor(data) {
    if (typeof data === "undefined")
      return
    const mode = store.manageMode(),
        oauth = data.descriptions[mode];
    this.title = data.title;
    this.name = data.name;
    this.email_label = oauth.form.email.label;
    this.email_placeholder = oauth.form.email.placeholder;
    this.password_label = oauth.form.password.label;
    this.password_placeholder = oauth.form.password.placeholder;
    this.connexion = oauth.form.connexion;
    this.create_account = oauth.form.create_account;
    this.create_account_url = oauth.form.create_account_url;
    this.forgot_password = oauth.form.forgot_password;
    this.forgot_password_url = oauth.form.forgot_password_url;
    this.error = oauth.form.error;
    this.sso_title = oauth.sso.title;
    this.sso_description = oauth.sso.description;
    this.sso_information = oauth.sso.information;
    this.sso_button = oauth.sso.button;
    this.sso_button_url = oauth.sso.button_url;
  }

}

</script>
