<template>
  <section class="payplugUiBlock generalBlock -login" data-e2e-name="generalLogin">
    <BlockHeader :blockHeaderTitle="translations.title"/>
    <form class="_loginForm">
      <InputLabel
          :inputLabelClass="'_email'"
          :inputLabelType="'email'"
          :inputLabelName="'payplug_email'"
          :inputLabelLabel="translations.email_label"
          :inputLabelPlaceholder="translations.email_placeholder"
          inputLabelData="payplugEmail"/>
      <InputLabel
          :inputLabelClass="'_password'"
          :inputLabelType="'password'"
          :inputLabelName="'payplug_password'"
          :inputLabelLabel="translations.password_label"
          :inputLabelPlaceholder="translations.password_placeholder"
          inputLabelData="payplugPassword"/>
      <div>
        <PayplugButton
            v-bind:class="(loading)?'-disabled':''"
            class="payplugUIButton _connexion"
            @click.prevent="doLogin"
            :buttonText="translations.connexion"
            data-e2e-name="payplugConnexion"/>
        <Anchor
            v-bind:class="(loading)?'-disabled':''"
            class="payplugUIButton _subscribe -tertiary"
            :anchorHref="translations.create_account_url"
            :anchorTitle="translations.create_account"
            :anchorText="translations.create_account"
            data-e2e-name="payplugCreateAccount"/>
      </div>
      <div>
        <div class="_forgotPassword_wrapper">
          <Anchor v-if="translations.forgot_password"
                  class="_forgotPassword"
                  :anchorHref="translations.forgot_password_url"
                  :anchorTitle="translations.forgot_password"
                  :anchorText="translations.forgot_password"
                  data-e2e-name="payplugForgotPassword"/>
        </div>
      </div>
      <Modal ref="errorModal" :modalContent="error_message" modalClose="Ok">
        <PayplugButton buttonText="Ok" @click.prevent="closeModlal"/>
      </Modal>
    </form>
  </section>
  <section class="payplugUiBlock generalBlock -oauth" data-e2e-name="generalOauthLogin">
    <TextAlert
        :textAlertText="translations.sso_information"
        textAlertType="-info"/>
    <BlockHeader :blockHeaderTitle="translations.sso_title" :blockHeaderDescription="translations.sso_description"/>
    <div>
      <ButtonLink
          class="payplugUIButton _connexion"
          :buttonLinkHref="translations.sso_button_url"
          :buttonLinkText="translations.sso_button"
          :buttonLinkTitle="translations.sso_button"
          buttonLinkTarget="_self"
          data-e2e-name="payplugSSOConnexion"
      />
    </div>
  </section>
</template>

<script>
import PayplugButton from "../atoms/AtomButton";
import Paragraph from "../atoms/AtomParagraph";
import ButtonLink from "../atoms/AtomButtonLink";
import BlockHeader from "../molecules/MoleculeBlockHeader";
import {store} from "@/store/store";
import InputLabel from "@/components/molecules/MoleculeInputLabel.vue";
import Anchor from "@/components/atoms/AtomAnchor.vue";
import AtomTextAlert from "@/components/atoms/AtomTextAlert.vue";
import TextAlert from "@/components/atoms/AtomTextAlert.vue";
import {$login} from "@/store/ajax";
import Modal from "@/components/molecules/MoleculeModal.vue";

export default {
  name: "OrganismSectionOauthLogin",
  components: {Modal, TextAlert, Anchor, InputLabel, ButtonLink, Paragraph, PayplugButton, BlockHeader, AtomTextAlert},
  computed: {
    translations: {
      get: () => store.translations.oauth_login
    },
    loading:{
      get: () => store.settings.loading.login
    },
    error_message(){
      if( store.error.login !== null && typeof store.error.login !== "undefined")
        this.showError();

      return store.error.login;
    },
  },
  methods: {
    doLogin() {
      store.settings.loading.login = true;
      store.data.payplug_email = document.getElementById('payplug_email').value;
      store.data.payplug_password = document.getElementById('payplug_password').value;
      $login();
    },
    showError(){
      this.$refs.errorModal.showModal();
    },
    closeModlal() {
      this.$refs.errorModal.closeModal();
    }
  }
}
</script>

