<script>

import {store} from "@/store/store";
import PayplugStandardWarningMessage from "@/classes/PaymentMethods/PaymentOptions/PayplugStandardWarningMessage";
import PayplugStandardInfoMessage from "@/classes/PaymentMethods/PaymentOptions/PayplugStandardInfoMessage";

export default class PayplugGenericGateway {

	type = null;
	name = null;
	title = null;
	image = null;
	checked = null;
	description = null;
	link_know_more = null;
	test_mode_avail = false;
	info_message = null;
	options = [];

	constructor(data) {
		let that = this;
		const mode = store.manageMode();
		this.type = data.type
		this.name = data.name
		this.title = data.title
		this.image = data.image
		this.checked = data.checked
		this.description = data.descriptions[mode].description;
		this.link_know_more = data.descriptions[mode].link_know_more;


		if(typeof data.options != "undefined"){
			data.options.map(function(payment_options_data){
				let payment_options = handlePaymentOptions(payment_options_data);

				if( payment_options != null ){

					if(payment_options.type === "warning_msg"){
						that.options.push(payment_options)
					}

					if(payment_options.type === "info_msg"){
						that.info_message = payment_options;
					}
				}
			});
		} else {
      that.options = null;
    }



		if (typeof data.available_test_mode !== "undefined") {
			this.test_mode_avail = data.available_test_mode;
		}

		function handlePaymentOptions(data) {
			switch (data.type) {
				case 'warning_message':
					return new PayplugStandardWarningMessage(data);
				// eslint-disable-next-line no-unreachable

				case 'info_message':
					return new PayplugStandardInfoMessage(data);
				// eslint-disable-next-line no-unreachable

				default:
					return null;
					// eslint-disable-next-line no-unreachable
					break;
			}
		}

	}

}

</script>
