<script>

import {store} from "@/store/store";

export default class PayplugApplePayCarriers {

	title = null;
	alert = null
	description = null;
	description_bold = null;
	description_warning = null;
	instructions = null;
	carriers_list = null;

	constructor(data)
	{
		const mode = store.manageMode();
		this.title = data.title;
		this.alert = data.alert;

		this.description = data.descriptions[mode].description;
		this.description_bold = data.descriptions[mode].description_bold;
		this.description_warning = data.descriptions[mode].description_warning;
		this.instructions = data.instructions;
		this.carriers_list = data.carriers_list;
	}

}


</script>
