<template>
  <div class="applePayCarrier">
    <div class="payplugUICheckbox">
      <label class="checkbox-label" :data-e2e-name="'carrier_' + carrier.id_carrier">
        <input type="checkbox" :name="'applepay_carriers[' + carrier.id_carrier + ']'" :checked="carrier.checked"
               @change="emitChange"/>
        <Paragraph :paragraphText="carrier.name"/>
      </label>
    </div>
  </div>
</template>

<script>
import Paragraph from "@/components/atoms/AtomParagraph";
import {store} from "@/store/store";

export default {
  name: "MoleculeApplePayCarrier",
  components: {Paragraph},
  data() {
    return {
      store: store,
    }
  },
  props: {
    carrier: {
      type: Object,
      required: true
    }
  },
  created() {
    let checked = this.carrier.checked || false,
        applepay_carriers = typeof this.store.data.applepay_carriers != 'undefined'
          ? this.store.data.applepay_carriers
          : [],
        id_carrier = this.carrier.id_carrier;
    if (checked) {
      applepay_carriers.push(id_carrier);
    }
    this.store.storeValue('applepay_carriers', applepay_carriers);
  },
  updated() {
    let checked = this.carrier.checked || false,
        applepay_carriers = this.store.data.applepay_carriers,
        id_carrier = this.carrier.id_carrier;
    if (checked) {
      applepay_carriers.push(id_carrier);
    } else {
      let pos = applepay_carriers.indexOf(id_carrier);
      applepay_carriers.splice(pos, 1);
    }
    this.store.storeValue('applepay_carriers', applepay_carriers);
  },
  methods:{
    emitChange(e) {
      let checked = e.target.checked,
          applepay_carriers = this.store.data.applepay_carriers,
          id_carrier = this.carrier.id_carrier;
      if (checked) {
        applepay_carriers.push(id_carrier);
      } else {
        let pos = applepay_carriers.indexOf(id_carrier);
        applepay_carriers.splice(pos, 1);
      }
      this.store.storeValue('applepay_carriers', applepay_carriers);
    }
  }
}
</script>

