<script>

import PayplugApplePayCarriers from "@/classes/PaymentMethods/PaymentOptions/PayplugApplePayCarriers.vue";

export default class PayplugApplePayCheckout {

	name = "applepay_display";
	type = "checkbox"
	title = null;
	checked = null
	options = null;
	carriers = null;
	multiple = null;

	constructor(data)
	{
		this.title = data.title;
		this.checked = data.checked;
		this.options = data.options;
		this.multiple = data.multiple;
		this.carriers = new PayplugApplePayCarriers(data.carriers)
	}

}

</script>

