<template>
  <div class="payplugUIIOptions" :class="optionsClassName">
    <template v-for="item in optionsItems" :key="item">
      <label :class="item?.className + ' ' + (item?.disabled ? '-disabled' : '')"
             :data-e2e-name="getE2e(optionsData, item?.value)"
             :id="optionsName+item.value">
        <input :type="optionsMultiple ? 'checkbox' : 'radio'"
               @change="updateOption"
               :name="optionsName"
               :value="item?.value"
               :checked="((optionsSelected && item?.value && optionsSelected == item?.value) || (item?.checked)) ? 'checked' : null"
               :disabled="(item?.disabled) ? 'disabled' : null"/>
        <span> {{ item?.label }} <span v-if="item?.subText">{{ item?.subText }}</span></span>
      </label>
    </template>
  </div>
</template>

<script>
import {store} from "@/store/store";
import {
  populate,
  getDataE2e, $enableIntegrated
} from "@/store/ajax"

export default {
  name: "AtomOptions",
  props: {
    optionsItems: {
      type: Object,
      required: true
    },
    optionsName: {
      type: String,
      required: false
    },
    optionsClassName: {
      type: String,
      required: false,
      default: ""
    },
    optionsMultiple: {
      type: Boolean,
      required: false,
      default: false
    },
    optionsSelected: {
      type: String,
      required: false
    },
    optionsData: {
      type: String,
      required: false
    },
  },
  data() {
    return {
      store: store,
      selected: null
    }
  },
  created() {
    let selected = this.optionsMultiple ? {} : '';
    if (this.optionsItems) {
      this.optionsItems.map(option => {
        if (this.optionsMultiple) {
          selected[option.value] = option.checked;
        } else if (option.checked) {
          selected = option.value;
        }
      })
    }

    this.selected = selected;
    this.store.storeValue(this.optionsName, selected)
  },
  methods: {
    async updateOption(e) {
      let item = e.target;
      var response = null;
      switch (this.optionsName) {
        case "payplug_embeded":
          store.storeValue(this.optionsName, item.value);
          this.$emit('update_embeded_option', item.value);
          response = await $enableIntegrated('integrated' == item.value);
          if (!response.success) {
            document.getElementById(this.optionsName + this.selected).click();
            store.error.payment_methods = response.data;
          } else {
            this.selected = item.value;
          }
          break;
        case "payplug_sandbox":
          store.storeValue(this.optionsName, item.value);
          this.$emit('update_embeded_option', item.value);
          this.selected = item.value;

          // If account inactive & in Live mode
          if(item.value == 0 && store.translations.logged.inactive_modal.inactive) {
            store.error_inactive = true;
            document.getElementById("payplug_sandbox1").click();
            return;
          }
          store.settings.mode = item.value;
          populate(store.rawData);
          break;
        case 'payplug_oney':
          store.storeValue(this.optionsName, item.value);
          break;
        case 'payplug_applepay_display':
          this.selected[item.value] = item.checked;
          store.storeValue(this.optionsName, this.selected);
          break;
        default:
          this.selected = item.value;
          break;
      }
    },
    getE2e(data, item) {
      item = this.handleE2EName(data, item);
      return getDataE2e(data, item)
    },
    handleE2EName(data, value){
      switch (data){
        case 'sandbox':
          return parseInt(value) ? 'Test' :  'Live';
        case 'oney':
          return this.handleOney(value);
        default:
          return value;
      }
    },
    handleOney(value){
      if(isNaN(value)){
        return value;
      }else{
        return parseInt(value) ? 'oneyWith_fees' :  'oneyWithout_fees';
      }
    }
  }
}
</script>

