<script>

import {store} from "@/store/store";

export default class PayplugMultiAccount {
	title = null;
	description = null;
  inputs = null;

	constructor(data)
	{
		if(typeof data === "undefined")
			return

		const mode = store.manageMode();
		this.title = data.title;
		this.description = data.descriptions[mode];
		this.inputs = data.inputs[mode];
	}

}

</script>
